import './layoutbox.scss'
import * as echarts from 'echarts'
import { useEffect, useReducer } from 'react'
import { Outlet, useNavigate } from 'react-router-dom';
import { FloatButton, Layout, message } from 'antd';
import { getTokenF } from '@/utils/token';
import AppHeader from '@/containers/AppHeader.jsx'
import AppAside from '@/containers/AppAside.jsx'
import AppFooter from '@/containers/AppFooter.jsx'
import { useDispatch } from 'react-redux';
import { clearToken } from '@/store/user';
import menu from '@/containers/menu'
import avatar from '@/assets/images/userlogo.png'


const { Content } = Layout

const MENU_TOGGLE = 'menuToggle'

const reducer = (state, action) => {
    switch (action.type) {
        case MENU_TOGGLE:
            return { ...state, menuToggle: !state.menuToggle }
        default:
            return state
    }
}
export default function LayoutBox() {
    const navigator = useNavigate()
    const tokenDispath = useDispatch()
    const [state, dispatch] = useReducer(reducer, { menuToggle: false })

    const menuClick = () => {
        dispatch({ type: 'menuToggle' })
    }
    const loginOut = () => {
        tokenDispath(clearToken())
        navigator('/login')
        message.success('登出成功!')
    }
    // 没有token跳转登录
    useEffect(() => {
        if (!getTokenF()) {
            navigator('/login')
        }

    }, [])

    return (
        <Layout className='app'>
            <FloatButton.BackTop visibilityHeight={0} />
            <AppAside menuToggle={state.menuToggle} menu={menu} />
            <Layout style={{ marginLeft: state.menuToggle ? '80px' : '200px', minHeight: '100vh' }}>
                <AppHeader menuToggle={state.menuToggle} menuClick={menuClick} avatar={avatar} loginOut={loginOut} />
                <Content className='content'>
                    <Outlet></Outlet>
                </Content>
                <AppFooter />
            </Layout>
        </Layout>)

}
