
import { Dropdown, Layout, Avatar, Badge} from 'antd'
import { LogoutOutlined, MenuUnfoldOutlined, MenuFoldOutlined, JavaScriptOutlined, BellOutlined, EditOutlined, SettingOutlined } from '@ant-design/icons';

import {UserOutlined } from '@ant-design/icons';
import './appHeader.scss'


const { Header } = Layout
const AppHeader = props => {
    let { menuClick, avatar, menuToggle, loginOut } = props
    const menu = [
        {
            label: (<div className='useropt'>用户设置</div>),
            key: '0'
        },
        {
            label: (<div><EditOutlined />个人设置</div>),
            key: '1'
        },
        {
            label: (<div><SettingOutlined />系统设置</div>),
            key: '2'
        },
        {
            label: (<div className="logoutBox" onClick={loginOut}><LogoutOutlined />退出登录</div>),
            key: '3'
        },

    ]
    return (
        <Header className='header'>
            <div className='left'>
                <span style={{ fontSize: '25px' }}
                    onClick={menuClick}>
                    {menuToggle ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
                </span>

            </div>
            <div className='right'>
                <div className='mr15'>
                    <a rel='noopener noreferrer' href='https://gitee.com/goldvvv/react-project' target='_blank'>
                        <JavaScriptOutlined style={{ color: '#000' }} />
                    </a>
                </div>
                <div className='mr15'>
                    <Badge dot={true} offset={[-2, 0]}>
                        <a href='https://gitee.com/goldvvv/react-project' style={{ color: '#000' }}>
                            <BellOutlined />
                        </a>
                    </Badge>
                </div>
                <div>
                    <Dropdown  menu={{ items:menu }} overlayStyle={{ width: '170px' }}>
                        <div className='ant-dropdown-link'>
                            <Avatar icon={<UserOutlined />} src={avatar} alt='avatar' style={{ cursor: 'pointer' }} />
                        </div>
                    </Dropdown>
                </div>
            </div>
        </Header>
    )
}



export default AppHeader
